import { Fragment, useState, useEffect } from "react";
import { Form, Card, Col, Row, Button, Alert } from "react-bootstrap";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import userServices from "../services/http/userServices";
import DatePicker from "react-multi-date-picker";
import persian from "react-date-object/calendars/persian";
import persian_fa from "react-date-object/locales/persian_fa";
import noticeServices from "../services/http/noticeServices";
import Configs from "../services/configs";
import "./profile.css";
import LoadingBar from "../common/LoadingBar";

const NoticeManager = () => {
    //u can create another component for listing notices ?
    const player = useSelector((state) => state.player);
    const [isAllowed, setAllowed] = useState(undefined);
    const [title, setTitle] = useState("");
    const [text, setText] = useState("");
    const [startDate, setStartDate] = useState(null);
    const [endDate, setEndDate] = useState(null);
    const [notices, setNotices] = useState([]);
    const [selectedNoticeID, setSelectedNoticeID] = useState(null);
    const [updateTrigger, setUpdateTrigger] = useState(false);
    const [loading, setLoading] = useState(false);

    const resetStates = () => {
        setTitle("");
        setText("");
        setStartDate(null);
        setEndDate(null);
        setSelectedNoticeID(null);
        setUpdateTrigger(!updateTrigger); // updateTrigger: true <===> false -> chane -> useEffects calls
    };

    useEffect(() => {
        //use another simpler preloader
        //load all notices in left side for selecting: load all and push them in notices state
        (async () => {
            try {
                setLoading(true);
                const { status, data } =
                    await noticeServices.getAdvancedNotics();
                if (status === Configs.Status.Successful) {
                    //return data.notices;
                    if (data.notices.length) setNotices(data.notices.reverse());
                    //if all is empty
                    else
                        setNotices([
                            { title: "", text: "   " },
                        ]);
                }
            } catch (err) {
                setNotices([
                    {
                        title: "",
                        text: "...      .   ",
                    },
                ]);
                setLoading(false);
            }
            setLoading(false);
        })();
    }, [updateTrigger]);

    //***** in server implemented a middleware for this, is this needed? */
    if (player && isAllowed === undefined) {
        //in admin tools pages: admin status is checked directly dor completely making sure that user is admin and he/her is trusted directly from server
        (async () => {
            try {
                const { data, status } = await userServices.isAdministrator(
                    player.userID
                );
                if (status === Configs.Status.Successful) return data.answer;
                return false;
            } catch (err) {
                //CHECK THIS PART
                return false;
            }
        })()
            .then((answer) => {
                setAllowed(answer);
            })
            .catch((err) => {
                if (!Configs.Status.isErrorExpected(err))
                    toast.error(
                        "       .    .",
                        { position: "top-left", closeOnClick: true }
                    );
            });
    }

    // show proper message for simple users
    if (!player || isAllowed === false) {
        //in first render a wronge message will be shown! find a fix
        //2nd condition must be exactly the same
        toast.warn("        .", {
            position: "top-left",
            closeOnClick: true,
        });
        return null;
    }

    //runs when create notice button clicks
    const createNewNotice = async () => {
        //add patern and stuff to states and inputs
        try {
            setLoading(true);
            const { status } = await noticeServices.createNotice({
                title,
                text,
                startDate: new Date(startDate),
                endDate: new Date(endDate),
            });
            //*********** */
            //COMPLETELY CHECK NOTICE IN CLIENT AND SERVER
            if (status === Configs.Status.CreatedSuccessfully) {
                toast.success(`  ${title}    `, {
                    position: "top-right",
                    closeOnClick: true,
                });
                resetStates();
            }
        } catch (err) {
            if (!Configs.Status.isErrorExpected(err))
                toast.error(
                    "      ...    ",
                    { position: "top-left", closeOnClick: true }
                );
        }
        setLoading(false);
    };

    const selecteNotice = (notice) => {
        // console.log(notice);
        setSelectedNoticeID(notice._id);
        setTitle(notice.title);
        setText(notice.text);
        setStartDate(new Date(notice.startDate));
        setEndDate(new Date(notice.endDate));
    };

    const cancelEditing = () => {
        resetStates();
    };

    const saveChanges = async () => {
        //add patern and stuff to states and inputs
        if (!selectedNoticeID) return;
        setLoading(true);
        try {
            const { status } = await noticeServices.editNotice(
                selectedNoticeID,
                { title, text, startDate, endDate }
            );
            if (status === Configs.Status.Successful) {
                toast.success(`  ${title}    `, {
                    position: "top-right",
                    closeOnClick: true,
                });
                resetStates();
            }
        } catch (err) {
            if (!Configs.Status.isErrorExpected(err))
                toast.error(
                    "      ...    ",
                    { position: "top-left", closeOnClick: true }
                );
        }
        setLoading(false);
    };
    // if admin status is checked directly by server then start rendering the page
    //consists two part" right part => used for creating and editing notices
    //left part lists all notices and allows admin to select one => after selecting the notice will be ready to be edited on right side

    //edit all heights in control panel
    //add patern and stuff to states and inputs
    return (
        <Fragment>
            {player && isAllowed && (
                <Row style={{ height: "100%" }}>
                    <LoadingBar loading={loading} />
                    <Col xs={5}>
                        <Card
                            border="success"
                            bg="transparent"
                            className="mx-auto notice-manager-card">
                            <Card.Header className="text-center">
                                 
                            </Card.Header>
                            <Card.Body className="text-right">
                                <Form.Label className="my-1 mx-auto">
                                     
                                </Form.Label>
                                <Form.Control
                                    value={title}
                                    onChange={(e) => setTitle(e.target.value)}
                                    pattern=".{3,30}"
                                    onInput={(e) =>
                                        e.target.setCustomValidity("")
                                    }
                                    onInvalid={(e) =>
                                        e.target.setCustomValidity(
                                            "    3   30   "
                                        )
                                    }
                                    className="my-1 mx-auto"
                                    type="text"
                                    placeholder=""
                                    required="required"
                                />
                                <hr />
                                <Form.Label className="my-1 mx-auto">
                                    
                                </Form.Label>
                                <Form.Control
                                    as="textarea"
                                    pattern=".{5,200}"
                                    onInput={(e) =>
                                        e.target.setCustomValidity("")
                                    }
                                    onInvalid={(e) =>
                                        e.target.setCustomValidity(
                                            "    5   200   "
                                        )
                                    }
                                    className="my-1 mx-auto h-25"
                                    placeholder="     "
                                    value={text}
                                    required="required"
                                    onChange={(e) => setText(e.target.value)}
                                />
                                <hr />
                                <Form.Label className="my-1 mx-2">
                                      
                                </Form.Label>

                                <DatePicker
                                    className="my-1 mx-auto w-100"
                                    calendar={persian}
                                    locale={persian_fa}
                                    calendarPosition="bottom-right"
                                    value={startDate}
                                    onChange={setStartDate}
                                    required="required"
                                />
                                <hr />
                                <Form.Label className="my-1 mx-2">
                                      
                                </Form.Label>

                                <DatePicker
                                    className="my-1 mx-auto"
                                    calendar={persian}
                                    locale={persian_fa}
                                    calendarPosition="bottom-right"
                                    value={endDate}
                                    onChange={setEndDate}
                                    required="required"
                                />
                                <hr />
                            </Card.Body>
                            <Card.Footer>
                                {!selectedNoticeID ? (
                                    <Button
                                        block
                                        variant="success"
                                        onClick={createNewNotice}>
                                        <i
                                            className="fa fa-floppy-o px-3"
                                            aria-hidden="true"></i>
                                         
                                    </Button>
                                ) : (
                                    <Row>
                                        <Col>
                                            <Button
                                                block
                                                variant="outline-primary"
                                                onClick={saveChanges}>
                                                <i
                                                    className="fa fa-floppy-o px-3"
                                                    aria-hidden="true"></i>
                                                 
                                            </Button>
                                        </Col>
                                        <Col>
                                            <Button
                                                block
                                                variant="outline-warning text-secondary"
                                                onClick={cancelEditing}>
                                                <i
                                                    className="fa fa-floppy-o px-3"
                                                    aria-hidden="true"></i>
                                                 
                                            </Button>
                                        </Col>
                                    </Row>
                                )}
                            </Card.Footer>
                        </Card>
                    </Col>
                    <Col xs={7}>
                        <Card
                            border="info"
                            bg="transparent"
                            className="mx-auto notice-manager-card">
                            <Card.Header className="text-center">
                                  
                            </Card.Header>
                            <Card.Body
                                style={{ overflowY: "scroll" }}
                                className="text-right">
                                {notices.map((notice) => {
                                    return (
                                        <Fragment>
                                            <Alert variant="secondary">
                                                <i
                                                    className="fa fa-info-circle px-3"
                                                    aria-hidden="true"></i>
                                                <span
                                                    style={{
                                                        color: "red",
                                                        fontSize: "18px",
                                                    }}>
                                                    {notice.title}
                                                </span>
                                                : {notice.text}
                                                <i
                                                    className="icon-edit-notice fa fa-pencil-square-o px-3"
                                                    aria-hidden="true"
                                                    onClick={() =>
                                                        selecteNotice(notice)
                                                    }></i>
                                            </Alert>
                                            <hr />
                                        </Fragment>
                                    );
                                })}
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            )}
        </Fragment>
    );
};

export default NoticeManager;
